<!--scr/components/app/MyPodcast.vue-->
<template>
  <div class="page_title">
    <p>
      Мої подкасти
    </p>
  </div>
  <div class="all-podcast-block">
    <div v-if="podcasts.length === 0" class="no-podcasts-message">
      <p>
        Тут відображатимуться куплені подкасти.<br>
        Придбайте свій перший подкаст <router-link to="/">вже зараз</router-link>.
      </p>
    </div>
    <div class="block_content_podcast" v-else>
    <div
        v-for="podcast in podcasts"
        :key="podcast.id"
        :class="['podcast', `id_${podcast.id}`]"
        :style="{ backgroundImage: `url(${podcast.image_url})`, backgroundRepeat: 'no-repeat', backgroundPosition: 'center' }"
        v-show="podcast.isVisible"
    >
      <div class="positions_in_block">
        <div class="upper_tittle">
          <div class="context">
            <button class="play-button" aria-label="Play podcast" @click="handlePlayButtonClick(podcast, $event)">
              <svg class="play-icon" xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 512 512">
                <path class="play-path" :d="isPlaying && playingPodcast === podcast.id ? pausePath : playPath" />
              </svg>
            </button>
            <div class="info_podcast">
                  <span class="title_podcast">
                    {{ truncateTitle(podcast.title) }}
                  </span>
              <span class="author_podcast">
                    {{ podcast.author }}
                  </span>
            </div>
          </div>
          <span class="time_podcast">
                {{ podcast.duration }}
            </span>
        </div>
        <div class="under_title">
          <div class="rating_and_categories">
            <div class="podcast-rating">{{ podcast.rating }}</div>
<!--            <span class="categories_podcast">{{ podcast.categories[0].name }}</span>-->
            <span class="categories_podcast">{{ podcast.categories && podcast.categories.length > 0 ? podcast.categories[0].name : 'Невідомо' }}</span>
          </div>
          <!--            isPodcastFavorite(podcast) ?-->
          <!--            podcast.isFavorite ?-->
          <button
              :class="['heart-button', podcast.isFavorite ? 'active' : '']"
              aria-label="favorite"
              @click="handleHeartButtonClick(podcast)"
          ></button>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
import apiService from '@/services/apiService';

export default {
  data() {
    return {
      podcasts: [],
      isPlaying: false,
      playingPodcast: null,
      playPath: 'M0 256a256 256 0 1 1 512 0A256 256 0 1 1 0 256zM188.3 147.1c-7.6 4.2-12.3 12.3-12.3 20.9V344c0 8.7 4.7 16.7 12.3 20.9s16.8 4.1 24.3-.5l144-88c7.1-4.4 11.5-12.1 11.5-20.5s-4.4-16.1-11.5-20.5l-144-88c-7.4-4.5-16.7-4.7-24.3-.5z',
      pausePath: 'M256 512A256 256 0 1 0 256 0a256 256 0 1 0 0 512zM224 192V320c0 17.7-14.3 32-32 32s-32-14.3-32-32V192c0-17.7 14.3-32 32-32s32 14.3 32 32zm128 0V320c0 17.7-14.3 32-32 32s-32-14.3-32-32V192c0-17.7 14.3-32 32-32s32 14.3 32 32z',
    };
  },
  mounted() {
    // Отримаємо подкасти для користувача

    const token = localStorage.getItem('token');
    apiService.getUserPodcasts(token)
      .then(data => {
        this.podcasts = data.data;
        //console.log('Number of podcasts:', this.podcasts.length);
      })
      .catch(error => {
        console.error("Error fetching podcasts:", error);
      });
  },
  methods: {
    //handlePlayButtonClick(podcast, event) {
      // логіка для відтворення/паузи
    //},
    //handleHeartButtonClick(podcast) {
      // логіка для додавання/видалення улюблених
    //},
    truncateTitle(title) {
      // логіка для обрізання заголовку
      return title;
    },
  },
}
</script>
<style>
.no-podcasts-message {
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
  //color: #888;
  color: #fff;
}
.no-podcasts-message a {
  color: #56a442;
}
</style>